@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
    font-family: 'Montserrat', sans-serif!important;
    background: #eaf0ea!important;
}

.nav-bg {
    background-color: #255f25bb!important;
}

.img-header {
    width: 20px;
}

.hr {
    width: 200px;
    margin: auto;
}

.pointer {
    cursor: pointer;
}

.header-link {
    text-decoration: none;
    color: #000;
}

.header-link:hover {
    text-decoration: none;
    color: #000;
}

.videoHome {
    max-width: 100%;
}

.img-premio img {
    padding: 2em;
    width: 330px;
    height: 300px;
    border-radius: 50%;
}

.img-3 {
    padding: 3em!important;
}

.MuiSlider-root {
    color: rgba(37, 95, 37, 0.733)!important;
}

.premio {
    width: 120px;
    height: 120px;
    background: no-repeat center;
    background-image: url(/static/media/premio.658e2c62.svg);
    margin: auto;
    color: transparent;
}

.premio-verde {
    width: 200px;
    height: auto;
    margin: auto;
}

.btn-outline-verde-home {
    background-color: #255f25bb!important;
    border-color: #255f25!important;
}

.logo-instagram i {
    color: #255f25bb!important
}

.texto_inicio {
    text-align: justify;
}

.texto_inicio p {
    margin: 8px;
}

.instagram-home {
    /*border-bottom: 1px #255f25 solid;*/
    transition: 0.5s;
    margin-left: 2px;
    color: #2f772f;
}

.instagram-home:hover {
    background-color: #255f25cc;
    border-radius: 12px;
    color: #fff;
}

.login-home {
    text-decoration: none;
    color: #ccc
}

.login-home:hover {
    text-decoration: none;
    color: #ccc
}
.botones button {
    width: 66px!important;
    height: 38px!important;
}

.c-espe input {
    width: 80%;
    background: none;
    border: none;
    border-radius: 0;
    border-bottom: #000 1px solid;
}

.c-espe input::-webkit-input-placeholder {
    color: #3a3939;
}

.c-espe input:-ms-input-placeholder {
    color: #3a3939;
}

.c-espe input::placeholder {
    color: #3a3939;
}

.c-espe input:focus {
    background: none;
}

.c-espe button {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    transition: 0.5s;
}

.c-espe button:hover {
    background-color: #000;
    color: #fff;
}

.c-espe .datos {
    padding-left: 14em;
}

@media screen and (max-width:982px) {
    .c-espe .datos {
        padding-left: 10em;
    }
}

@media screen and (max-width:768px) {
    .c-espe .datos {
        padding-left: 2em;
    }
}

.formulario-datos input {
    background: #d9d9d9;
    border-radius: 15px;
}

.formulario-datos input:focus {
    background: #d9d9d9;
}

.btn-outline-verde {
    border-color: #255f25!important;
}

.btn-outline-verde:hover {
    background-color: #255f25bb!important;
}

.btn-outline-verde-signos:hover {
    background-color: #255f25bb!important;
}
.formulario-datos input, textarea {
    background: #d9d9d9!important;
    border-radius: 15px!important;
}

.formulario-datos button {
    width: 200px;
}

.formulario-datos input:focus {
    background: #d9d9d9;
}

.btn-outline-verde-contacto {
    background-color: #255f25bb!important;
    border-color: #255f25!important;

}
.premios img{
    width: 400px;
    margin: auto!important;
}
.parlante img{
    width: 300px;
    margin: auto!important;
}
